'use client';

import { useEffect, useState } from 'react';
import { VpnServer, ConnectionHistory } from '@/hooks/useVpn';

interface VpnDashboardProps {
  isConnected: boolean;
  isConnecting: boolean;
  selectedServer: VpnServer | null;
  connectionHistory: ConnectionHistory[];
  error: string | null;
  connectedAt: Date | null;
  onConnect: () => void;
  onDisconnect: () => void;
  onClearError: () => void;
  getConnectionDuration: () => number;
}

export const VpnDashboard = ({
  isConnected,
  isConnecting,
  selectedServer,
  connectionHistory,
  error,
  connectedAt,
  onConnect,
  onDisconnect,
  onClearError,
  getConnectionDuration
}: VpnDashboardProps) => {
  const [duration, setDuration] = useState(0);

  // C·∫≠p nh·∫≠t th·ªùi gian k·∫øt n·ªëi m·ªói gi√¢y
  useEffect(() => {
    if (!isConnected) return;

    const interval = setInterval(() => {
      setDuration(getConnectionDuration());
    }, 1000);

    return () => clearInterval(interval);
  }, [isConnected, getConnectionDuration]);

  const formatDuration = (seconds: number) => {
    const hours = Math.floor(seconds / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);
    const secs = seconds % 60;

    if (hours > 0) {
      return `${hours}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
    }
    return `${minutes}:${secs.toString().padStart(2, '0')}`;
  };

  const formatHistoryDuration = (seconds?: number) => {
    if (!seconds) return 'N/A';
    const minutes = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${minutes}:${secs.toString().padStart(2, '0')}`;
  };

  return (
    <div className="space-y-6">
      {/* Tr·∫°ng th√°i k·∫øt n·ªëi */}
      <div className="bg-white dark:bg-gray-800 rounded-lg p-6 border border-gray-200 dark:border-gray-700">
        <div className="text-center space-y-4">
          {/* Indicator tr·∫°ng th√°i */}
          <div className="flex items-center justify-center">
            <div className={`
              w-16 h-16 rounded-full flex items-center justify-center
              ${isConnected ? 'bg-green-100 dark:bg-green-900/30' : 
                isConnecting ? 'bg-yellow-100 dark:bg-yellow-900/30' : 
                'bg-gray-100 dark:bg-gray-700'}
            `}>
              <div className={`
                w-8 h-8 rounded-full
                ${isConnected ? 'bg-green-500' : 
                  isConnecting ? 'bg-yellow-500 animate-pulse' : 
                  'bg-gray-400'}
              `}></div>
            </div>
          </div>

          {/* Tr·∫°ng th√°i text */}
          <div>
            <h2 className="text-2xl font-bold text-gray-900 dark:text-white">
              {isConnected ? 'ƒê√£ k·∫øt n·ªëi' : 
               isConnecting ? 'ƒêang k·∫øt n·ªëi...' : 
               'Ch∆∞a k·∫øt n·ªëi'}
            </h2>
            
            {isConnected && selectedServer && (
              <div className="mt-2 space-y-1">
                <p className="text-lg text-gray-600 dark:text-gray-300">
                  {selectedServer.name}, {selectedServer.country}
                </p>
                <p className="text-sm text-gray-500 dark:text-gray-400">
                  Th·ªùi gian k·∫øt n·ªëi: {formatDuration(duration)}
                </p>
                <p className="text-sm text-gray-500 dark:text-gray-400">
                  Ping: {selectedServer.ping}ms
                </p>
              </div>
            )}

            {!isConnected && !isConnecting && selectedServer && (
              <p className="text-gray-600 dark:text-gray-300 mt-2">
                S·∫µn s√†ng k·∫øt n·ªëi ƒë·∫øn {selectedServer.name}
              </p>
            )}

            {!selectedServer && !isConnecting && (
              <p className="text-gray-500 dark:text-gray-400 mt-2">
                Ch·ªçn m√°y ch·ªß ƒë·ªÉ b·∫Øt ƒë·∫ßu
              </p>
            )}
          </div>

          {/* N√∫t ƒëi·ªÅu khi·ªÉn */}
          <div className="flex gap-3 justify-center">
            {!isConnected && !isConnecting && (
              <button
                onClick={onConnect}
                disabled={!selectedServer}
                className={`
                  px-8 py-3 rounded-lg font-medium transition-all duration-200
                  ${selectedServer
                    ? 'bg-green-600 hover:bg-green-700 text-white shadow-lg hover:shadow-xl'
                    : 'bg-gray-300 dark:bg-gray-600 text-gray-500 dark:text-gray-400 cursor-not-allowed'
                  }
                `}
              >
                K·∫øt n·ªëi VPN
              </button>
            )}

            {isConnecting && (
              <button
                disabled
                className="px-8 py-3 rounded-lg font-medium bg-yellow-500 text-white cursor-not-allowed"
              >
                ƒêang k·∫øt n·ªëi...
              </button>
            )}

            {isConnected && (
              <button
                onClick={onDisconnect}
                className="px-8 py-3 rounded-lg font-medium bg-red-600 hover:bg-red-700 text-white shadow-lg hover:shadow-xl transition-all duration-200"
              >
                Ng·∫Øt k·∫øt n·ªëi
              </button>
            )}
          </div>
        </div>
      </div>

      {/* Th√¥ng b√°o l·ªói */}
      {error && (
        <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg p-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="w-5 h-5 rounded-full bg-red-500 flex items-center justify-center">
                <span className="text-white text-xs font-bold">!</span>
              </div>
              <p className="text-red-800 dark:text-red-200">{error}</p>
            </div>
            <button
              onClick={onClearError}
              className="text-red-600 dark:text-red-400 hover:text-red-800 dark:hover:text-red-200 font-medium"
            >
              ƒê√≥ng
            </button>
          </div>
        </div>
      )}

      {/* L·ªãch s·ª≠ k·∫øt n·ªëi */}
      {connectionHistory.length > 0 && (
        <div className="bg-white dark:bg-gray-800 rounded-lg p-6 border border-gray-200 dark:border-gray-700">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
            L·ªãch s·ª≠ k·∫øt n·ªëi
          </h3>
          
          <div className="space-y-3 max-h-64 overflow-y-auto">
            {connectionHistory.map((connection) => (
              <div
                key={connection.id}
                className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700 rounded-lg"
              >
                <div>
                  <p className="font-medium text-gray-900 dark:text-white">
                    {connection.server}
                  </p>
                  <p className="text-sm text-gray-500 dark:text-gray-400">
                    {connection.connectedAt.toLocaleString('vi-VN')}
                  </p>
                </div>
                
                <div className="text-right">
                  <p className="text-sm font-medium text-gray-900 dark:text-white">
                    {connection.disconnectedAt ? 'ƒê√£ ng·∫Øt' : 'ƒêang k·∫øt n·ªëi'}
                  </p>
                  {connection.duration && (
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                      {formatHistoryDuration(connection.duration)}
                    </p>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};
