'use client';

import { useState, useCallback } from 'react';

export interface VpnServer {
  id: string;
  name: string;
  country: string;
  city: string;
  ping: number;
  load: number;
}

export interface ConnectionHistory {
  id: string;
  server: string;
  connectedAt: Date;
  disconnectedAt?: Date;
  duration?: number;
}

export const useVpn = () => {
  const [isConnected, setIsConnected] = useState(false);
  const [isConnecting, setIsConnecting] = useState(false);
  const [selectedServer, setSelectedServer] = useState<VpnServer | null>(null);
  const [connectionHistory, setConnectionHistory] = useState<ConnectionHistory[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [connectedAt, setConnectedAt] = useState<Date | null>(null);

  // Danh s√°ch m√°y ch·ªß VPN m·∫´u (m√¥ ph·ªèng)
  const availableServers: VpnServer[] = [
    { id: '1', name: 'H√† N·ªôi', country: 'Vi·ªát Nam', city: 'H√† N·ªôi', ping: 15, load: 45 },
    { id: '2', name: 'TP.HCM', country: 'Vi·ªát Nam', city: 'TP.HCM', ping: 12, load: 60 },
    { id: '3', name: 'Singapore', country: 'Singapore', city: 'Singapore', ping: 25, load: 30 },
    { id: '4', name: 'Tokyo', country: 'Nh·∫≠t B·∫£n', city: 'Tokyo', ping: 45, load: 55 },
    { id: '5', name: 'Seoul', country: 'H√†n Qu·ªëc', city: 'Seoul', ping: 40, load: 35 },
    { id: '6', name: 'Hong Kong', country: 'Hong Kong', city: 'Hong Kong', ping: 30, load: 70 },
  ];

  const selectServer = useCallback((server: VpnServer) => {
    if (isConnecting || isConnected) {
      setError('Vui l√≤ng ng·∫Øt k·∫øt n·ªëi tr∆∞·ªõc khi ch·ªçn m√°y ch·ªß kh√°c');
      return;
    }
    setSelectedServer(server);
    setError(null);
  }, [isConnecting, isConnected]);

  const connectVpn = useCallback(async () => {
    if (!selectedServer) {
      setError('Vui l√≤ng ch·ªçn m√°y ch·ªß VPN tr∆∞·ªõc khi k·∫øt n·ªëi');
      return;
    }

    if (isConnected || isConnecting) {
      return;
    }

    setIsConnecting(true);
    setError(null);

    try {
      // M√¥ ph·ªèng th·ªùi gian k·∫øt n·ªëi (1-3 gi√¢y)
      const connectionTime = Math.random() * 2000 + 1000;
      
      await new Promise((resolve, reject) => {
        setTimeout(() => {
          // M√¥ ph·ªèng kh·∫£ nƒÉng k·∫øt n·ªëi th·∫•t b·∫°i (10% c∆° h·ªôi)
          if (Math.random() < 0.1) {
            reject(new Error('Kh√¥ng th·ªÉ k·∫øt n·ªëi ƒë·∫øn m√°y ch·ªß VPN'));
          } else {
            resolve(true);
          }
        }, connectionTime);
      });

      const now = new Date();
      setIsConnected(true);
      setConnectedAt(now);
      
      // Th√™m v√†o l·ªãch s·ª≠ k·∫øt n·ªëi
      const newConnection: ConnectionHistory = {
        id: Date.now().toString(),
        server: `${selectedServer.name}, ${selectedServer.country}`,
        connectedAt: now,
      };
      
      setConnectionHistory(prev => [newConnection, ...prev.slice(0, 9)]); // Gi·ªØ 10 k·∫øt n·ªëi g·∫ßn nh·∫•t
      
    } catch (err) {
      setError(err instanceof Error ? err.message : 'L·ªói k·∫øt n·ªëi kh√¥ng x√°c ƒë·ªãnh');
    } finally {
      setIsConnecting(false);
    }
  }, [selectedServer, isConnected, isConnecting]);

  const disconnectVpn = useCallback(() => {
    if (!isConnected) {
      return;
    }

    setIsConnected(false);
    setIsConnecting(false);
    setError(null);

    // C·∫≠p nh·∫≠t l·ªãch s·ª≠ k·∫øt n·ªëi
    if (connectedAt) {
      const now = new Date();
      const duration = Math.floor((now.getTime() - connectedAt.getTime()) / 1000);
      
      setConnectionHistory(prev => 
        prev.map((conn, index) => 
          index === 0 ? { ...conn, disconnectedAt: now, duration } : conn
        )
      );
    }
    
    setConnectedAt(null);
  }, [isConnected, connectedAt]);

  const clearError = useCallback(() => {
    setError(null);
  }, []);

  const getConnectionDuration = useCallback(() => {
    if (!isConnected || !connectedAt) return 0;
    return Math.floor((new Date().getTime() - connectedAt.getTime()) / 1000);
  }, [isConnected, connectedAt]);

  return {
    // States
    isConnected,
    isConnecting,
    selectedServer,
    connectionHistory,
    error,
    availableServers,
    connectedAt,
    
    // Actions
    selectServer,
    connectVpn,
    disconnectVpn,
    clearError,
    getConnectionDuration,
  };
};
